* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, serif;
  font-size: 100px;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.blocks {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  color: white;
  height: 300px;
  flex-basis: 300px;
}

@media (min-width: 900px) {
  .blocks {
    flex-basis: 33%;
  }
  
  .block--4 {
    order: 2;
  }
  
  .block--5 {
    order: 1;
  }

  .block--6 {
    order: 0;
  }
}

@media (min-width: 600px) and (max-width: 899px) {
  .blocks {
    flex-basis: 50%;
  }

  .block--3 {
    order: 1;
  }
  
  .block--4 {
    order: 0;
  }

  .block--5 {
    order: 1;
  }

  .block--6 {
    order: 1;
  }
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: rgb(80%,0%,0%);
}

.block--3 {
  background-color: rgb(60%,0%,0%);
}

.block--4 {
  background-color: rgb(40%,0%,0%);
}

.block--5 {
  background-color: rgb(20%,0%,0%);
}

.block--6 {
  background-color: rgb(0%,0%,0%);
}
